

import React, { lazy,Suspense,Component } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import {
  CHeader,
  CToggler,
  CHeaderBrand,
  CHeaderNav,
  CBadge,
  CButton,
  CButtonGroup,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CProgress,
  CRow,
  CCallout
} from '@coreui/react'
import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  CardHeader,
  CardTitle,ListGroup,ListGroupItem,
  FormGroup,
  Label,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
  Spinner
} from "reactstrap"
import CIcon from '@coreui/icons-react'

import logo from '../../../src/assets/ot.png'
import ModalMessage from '../pages/messages/ModalMessage'

import { CContainer, CFade } from '@coreui/react'
import { format } from 'date-fns';
import ReactSelect from 'react-select'
import makeAnimated from 'react-select/animated';
import  { Redirect,Route } from 'react-router-dom'
import url from '../../conf.js';
import { storage } from "../../firebase/firebase";
import $ from 'jquery'
import conf from '../../conf.js';
import ville from '../dict/ville.js'
import Flatpickr from "react-flatpickr"
import { French } from "flatpickr/dist/l10n/fr.js";
import TheHeaderVisiteur from '../../containers/TheHeaderVisiteur'
import TheFooter from '../../containers/TheFooter'



const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

class Indisponibilite extends Component {
  constructor(props) {
      super(props)
      this.state = {
        heure_debut:"",
        heure_fin:"",
        date:"",
        status:true,
        terrain:{},
        show:false,
        success:true,
        boutton:"OK",
        message:"Indisponibilte enregistré avec succès !!",
        titre:"Message Indisponibilte",
        temps:[],
        jours:[],
        disponible:false,
        disponibilite:{},
        default_time:["00H-01H","01H-02H","02H-03H","03H-04H","04H-05H","05H-06H","06H-07H","07H-08H","08H-09H","09H-10H","10H-11H",
        "11H-12H","12H-13H","13H-14H","14H-15H","15H-16H","16H-17H","17H-18H","18H-19H","19H-20H","20H-21H","21H-22H","22H-23H","23H-24H"],
        
        default_days:["LUNDI","MARDI","MERCREDI","JEUDI","VENDREDI","SAMEDI","DIMANCHE"],
        lundi:"",
        temps_date:[],
        jour_valide:true,
        page2:"",
        day_load:false
      }
      this.handleDate.bind(this)
      this.handleClickJour.bind(this)
      this.handleClickHeure.bind(this)
      this.handleTraiteDate.bind(this)

  }
  
  componentDidMount() {
    $("#echec").css("display","none")
    $("#champ-heure").hide()
    $("#champ-heure-bouton").hide()
    this.setState({temps:this.state.default_time})
    this.setState({temps_date:this.state.default_time})
    this.setState({jours:this.state.default_days})
    
    console.log("jr",new Date().getDay())
    

    let  data=  JSON.parse(localStorage.getItem('token_auth'))
    console.log("user",data)
    var id=localStorage.getItem("idTerrain")
    const requestOptions = {
      method: 'GET',
      headers: { 
        'Authorization':'Bearer '+data.token,
  
      },
    };

    fetch(conf.url+"terrain/terrains/"+id,requestOptions)
    .then((response) => response.json())
    .then((data) => {
      
      this.setState({terrain:data})
      $("#champ-heure").hide()
      $("#champ-heure-bouton").hide()

      if(data.indisponibilite!=null){
        this.setState({disponible:true,disponibilite:data.indisponibilite})
        try{
          data.indisponibilite.day.split(";").map((el)=>{
            $("#"+el.toLowerCase()).prop( "checked", true )
          })
        }catch(err){

        }
        try{
         
          this.state.disponibilite.heure.split(";").map((el)=>{
            $("#"+el).prop( "checked", true )
           
          })
        }catch(err){

        }
      }else{
        this.setState({disponible:false})
      }
      
      
      })
  }


  handleDate=(date)=>{
    this.setState({day_load:false,page2:<Spinner size="md" color="success" />,temps_date:this.state.default_time})
    setTimeout(()=>{
      this.handleTraiteDate(date); }, 3000);
  }

  handleTraiteDate= (date)=> {

     const jr= {"LUNDI":1,"MARDI":2,"MERCREDI":3,"JEUDI":4,"VENDREDI":5,"SAMEDI":6,"DIMANCHE":7}
    
    
  
      if(date.length!=0){
        
        $("#champ-heure").show()
        $("#champ-heure-bouton").show()
        console.log("date:",format(date[0],'dd-MM-yyyy'))
  
        let  token=  JSON.parse(localStorage.getItem('token_auth'))
        const terrain=localStorage.getItem("idReservationTerrain")
        const requestOptions = {
          method: 'POST',
          headers: { 'Content-Type': 'application/json','Accept': 'application/json',
          'Authorization':'Bearer '+token.token,
        },
        body: JSON.stringify({date:format(date[0],'dd/MM/yyyy'),idTerrain:terrain})
        
        };
    
        fetch(conf.url+"terrain/reservations",requestOptions)
        .then((response) => response.json())
        .then((data) => {
          console.log("data reser",data)
          var reserv=";"
          if(data.length){
            reserv=data[0]
          }
 
      
          try{
            
            if(this.state.disponible){
              reserv=reserv+this.state.disponibilite.heure
             
              try{
                for(var i=0;i<this.state.disponibilite.day.split(";").length;i++){
                  if(jr[this.state.disponibilite.day.split(";")[i]]==date[0].getDay()){
                    this.setState({jour_valide:false})
                    break
                  }
                }
  
              }catch(err){
           
  
              }
 
              reserv.split(';').map((element)=>{
               console.log("data ;",element)
               this.setState({temps_date:this.state.temps_date.filter(val=>val!=element),day_load:true})
             
             })
  
            }else{
             if(data.length) {
             data[0].split(';').map((element)=>{
               console.log("data ;",element)
               this.setState({temps_date:this.state.temps_date.filter(val=>val!=element),day_load:true})
             
             })
            }else{
             this.setState({temps_date:this.state.default_time,day_load:true})
           }
 
           }
  
  
          
  
          }catch(error){
 
           this.setState({temps_date:this.state.default_time,day_load:true})
 
     
          }
          
        
      
       })
        
        this.setState({date:format(date[0],'dd/MM/yyyy')});
      }else{
        $("#champ-heure").css("display","none")
      }
    
      
    }

  handleReserver=()=>{
   
    const terrain=localStorage.getItem("idTerrain")
    let  token=  JSON.parse(localStorage.getItem('token_auth'))

    let time=""
    let day=""
    let total1=0;
    let total2=0;
   
   

    this.state.temps_date.map((val,idx)=>{
      if ($('#checkbox2'+idx).is(":checked"))
      {
        time=time+$('#checkbox2'+idx).val()+";"
        //total2+=parseInt(this.state.terrain.prixHeure)
      }
    })



    var data= {
  
      "dateReservation":this.state.date,
      "statePayement":"PENDING",
      "heure":time,
      "idTerrain":terrain,
      "nomTerrain":this.state.terrain.nom,
      "montant":this.state.terrain.prixHeure,
      "montantTotal":0,
      "idUserDoReservation":token.user.id
     
     
  
    }

    console.log("data a transmettre",data)

    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json','Accept': 'application/json',
      'Authorization':'Bearer '+token.token,
    },
      body: JSON.stringify(data)
  };

    fetch(conf.url+"user/add/reservation/user/"+token.user.id+"/terrain/"+terrain,requestOptions)
    .then((response) => response.json())
    .then((data) => {
       console.log("okkk====>",data)
       
    try{
      if(data.id){
       this.setState({show:true,success:true});

      }else{
       this.setState({show:true,success:false});
      }
      }catch(err){
     this.setState({show:true,success:false});
    }
       
    
     }
     
    );
  

    
  } 
  handleCheck=(e)=>{
     console.log("value:",e)
  }

  handleValidate=()=>{
    if(this.state.success){
      window.location.reload(false);
    }else{
      this.setState({show:false})
    }
    
  }

  handleClickJour=(data)=>{
    
    if($('#'+data).is(":checked")){
      $('#'+data).removeAttr( "checked" );
    }else{
      $('#'+data).attr( "checked" );
    }
    
  }

  handleClickHeure=(data)=>{
   
    if($('#'+data).is(":checked")){
      $('#'+data).removeAttr( "checked" );
    }else{
      $('#'+data).attr( "checked" );
    }
    
  }

  handleAddDisponibilite=()=>{
    const terrain=localStorage.getItem("idTerrain")
    let  token=  JSON.parse(localStorage.getItem('token_auth'))

    let heure=""
    let day=""
   
   
    this.state.jours.map((val,idx)=>{
      if ($('#'+val.toLowerCase()).is(":checked"))
      {
        day=day+$('#'+val.toLowerCase()).val()+";"
       
      }
    })

    this.state.temps.map((val,idx)=>{
      if ($('#'+val).is(":checked"))
      {
        heure=heure+$('#'+val).val()+";"
       
      }
    })



    var data= {
  
      "day":day,
      "heure":heure,
    
    }

    console.log("data a transmettre",data)

    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json','Accept': 'application/json',
      'Authorization':'Bearer '+token.token,
    },
      body: JSON.stringify(data)
  };
 
    fetch(conf.url+"indisponibilite/add/terrain/"+terrain,requestOptions)
    .then((response) => response.json())
    .then((data) => {
       console.log("okkk====>",data)
       
    try{
      if(data.id){
       this.setState({show:true,success:true});
       window.location.reload(false);
      }else{
       this.setState({show:true,success:false});
      }
      }catch(err){
     this.setState({show:true,success:false});
    }
       
    
     }
     
    );
  

  }

  handleUpdateDays=()=>{

    const terrain=localStorage.getItem("idTerrain")
    let  token=  JSON.parse(localStorage.getItem('token_auth'))

    let heure=""
    let day=""
   
   
    this.state.jours.map((val,idx)=>{
      if ($('#'+val.toLowerCase()).is(":checked"))
      {
        day=day+$('#'+val.toLowerCase()).val()+";"
       
      }
    })

  



    var data= {
       "id":this.state.disponibilite.id,
      "day":day,
      "heure":this.state.disponibilite.heure,
    
    }

    console.log("data a transmettre",data)

    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json','Accept': 'application/json',
      'Authorization':'Bearer '+token.token,
    },
      body: JSON.stringify(data)
  };
 
    fetch(conf.url+"indisponibilite/update/",requestOptions)
    .then((response) => response.json())
    .then((data) => {
       console.log("okkk====>",data)
       
    try{
      if(data.id){
       this.setState({show:true,success:true});
       window.location.reload(false);
      }else{
       this.setState({show:true,success:false});
      }
      }catch(err){
     this.setState({show:true,success:false});
    }
       
    
     }
     
    );

  }

  handleUpdateHour=()=>{

    let  token=  JSON.parse(localStorage.getItem('token_auth'))

    let heure=""
    let day=""
   
   
    this.state.temps.map((val,idx)=>{
      if ($('#'+val).is(":checked"))
      {
        heure=heure+$('#'+val).val()+";"
       
      }
    })

  



    var data= {
      "id":this.state.disponibilite.id,
      "day":this.state.disponibilite.day,
      "heure":heure,
    
    }

    console.log("data a transmettre",data)

    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json','Accept': 'application/json',
      'Authorization':'Bearer '+token.token,
    },
      body: JSON.stringify(data)
  };
 
    fetch(conf.url+"indisponibilite/update/",requestOptions)
    .then((response) => response.json())
    .then((data) => {
       console.log("okkk====>",data)
       
    try{
      if(data.id){
       this.setState({show:true,success:true});
       window.location.reload(false);
      }else{
       this.setState({show:true,success:false});
      }
      }catch(err){
     this.setState({show:true,success:false});
    }
       
    
     }
     
    );

  }

  render() {
    const animatedComponents = makeAnimated();
    const { pick } = this.state
    const lundi=""
    const dateOuHeure = [
      {
        options: [
          { label: "Jour précis", value: "JourPrecis" },
          { label: "Jour et Heure précise", value: "JourHeure" },
        ]
      },
    ]

  return (
    <>

    <CContainer fluid>
    <CRow className="justify-content-center">
    <Suspense fallback={loading}>
    {
     (this.state.status)?
    (
    <CFade>
   

   <ModalMessage
      show={this.state.show}
      success={this.state.success}
      boutton={this.state.boutton}
      handleValidate={this.handleValidate}
      message={this.state.message}
      titre={this.state.titre}
      onCloseClick={() => this.setState({ show: false })}
    />

      {/* <div className="mt-5"></div> */}

      <section style={{marginTop:"0px"}} id="contact" class="contact">
        <div class="container">

          <div className="mt-1">
            <div class="section-title">
              <h2>Les indisponibilités du terrain - {this.props.terrain}</h2>
            </div>
          </div>
          

          <div class="row">
            <div class="col-lg-7 mt-5 mt-lg-0 d-flex align-items-stretch" className="justify-content-center">
              <div  class="php-email-form">
                <div class="form-row">
                  <div class="container">
                    <div class="form-group col-md-12">
                      <h2 for="name" className="text-center">Choisissez les jours d'indisponibilité de ce terrain</h2>
                      <p for="name" className="text-center">Ce terrain sera <strong>toujours indisponible</strong> pour les <strong>jours </strong> qui seront sélectionnées ci-dessous :</p>
                      
                      <div class="validate"></div>
                    </div>
                    <ul class="cboxtags1">
                      {
                        this.state.jours.map((element,idx)=>(
                          <li className="mr-2"><input  type="checkbox"  onClick={this.handleClickJour.bind(this,element.toLowerCase())}  id={element.toLowerCase()} value={element} /><label for={element.toLowerCase()}>{element}</label></li>
                        ))
                      }
                   
                    </ul>

                    {
                    this.state.disponible?
                    <div class="form-row" clasName="mb-3">
                      <div class="container">
                        <div class="text-center"><button onClick={this.handleUpdateDays}  id="ajoutliste">Enregistrer la modification</button></div>
                      </div>
                    </div>:null
                    }


                  </div>
                </div>
              </div>  
            </div>
          </div>

                <hr></hr>

          <div class="row">
            <div class="col-lg-7 mt-5 mt-lg-0 d-flex align-items-stretch" className="justify-content-center">
              <div  class="php-email-form">
                <div class="form-row">
                  <div class="container">
                    <div class="form-group col-md-12">
                      <h2 for="name" className="text-center">Choisissez les heures d'indisponibilité de ce terrain</h2>
                      <p for="name" className="text-center">Ce terrain sera <strong>toujours indisponible</strong> pour les <strong>heure </strong>qui seront sélectionnées ci-dessous :</p>
                      
                      <div class="validate"></div>
                    </div>
                    <ul class="cboxtags2">
                      {
                        this.state.temps.map((element,idx)=>(
                          <li className="mr-2"><input type="checkbox" onClick={this.handleClickHeure.bind(this,element)}  id={element} value={element} /><label for={element}>{element}</label></li>
                        ))
                      }
                    </ul>
               {
                 this.state.disponible?
                <div class="form-row" clasName="mb-3">
                  <div class="container">
                    <div class="text-center"><button onClick={this.handleUpdateHour} id="ajoutliste">Enregistrer la modification</button></div>
                  </div>
                </div>:null
              }
                  </div>

                

                </div>
              </div>  
            </div>
           
          </div>

          <hr></hr>

          <div class="row">
            <div class="col-lg-7 mt-5 mt-lg-0 d-flex align-items-stretch" className="justify-content-center">
              <div  class="php-email-form">
                
                <div class="container">
                  <h2 for="name" className="text-center">Choisissez une date précise d'indisponibilité de ce terrain</h2>
                  <p for="name" className="text-center">Ce terrain sera indisponible <strong>uniquement</strong> pour <strong>la date et/ou l'heure précise</strong> qui sera sélectionnée ci-dessous :</p>
                </div>
                
                <div class="form-row mt-2">
                    <div class="form-group col-lg-6 col-md-6">
                      <div className="docs-datepicker">
                        <InputGroup>
                          <div className="input-group-append">
                            <button
                              type="button"
                              className="btn btn-outline-secondary docs-datepicker-trigger"
                              disabled
                            >
                              <i
                                className="fa fa-calendar"
                                aria-hidden="true"
                              />
                            </button>
                          </div>
                          <Flatpickr
                            value={pick}
                            className="form-control d-block"
                            placeholder="Choisissez la date"
                            options={{
                              minDate: "today",
                              locale:French,
                              altInput: true,
                              altFormat: "d/M/Y"
                            }}
                            onChange={this.handleDate}
                          />
                        </InputGroup>
                        <div className="docs-datepicker-container" />
                      </div>
                      <div class="validate"></div>
                    </div>
                  
                
                    {/* <div class="form-group col-lg-6 col-md-6">
                      
                      <ReactSelect
                        defaultValue={{label:"Jour précis",value:"JourPrecis"}}
                        closeMenuOnSelect={true}
                        components={animatedComponents}
                       
                        options={dateOuHeure}
                        placeholder="Choisir le niveau"
                      />
                    </div> */}
                </div>
              
               
               {
                 (this.state.day_load)?  
                (((this.state.jour_valide) && (this.state.temps_date.length))?
                <>
                <div class="form-row" id="champ-heure">
                  <div class="container">
                    <ul class="cboxtags3">
                      {
                        this.state.temps_date.map((element,idx)=>(
                          <li className="mr-2"><input type="checkbox"  id={"checkbox2"+idx} value={element} /><label for={"checkbox2"+idx}>{element}</label></li>
                        ))
                      }
                    </ul>
                  </div>
                </div>
                <div class="form-row" clasName="mb-3" id="champ-heure-bouton">
                  <div class="container">
                    <div class="text-center"><button onClick={this.handleReserver} id="ajoutliste">Réserver</button></div>
                  </div>
                </div>
                </>:<h2>{"Impossible de réserver a cette date!!"}</h2>):(<>{this.state.page2}</>)
              }

                <div class="form-row" clasName="mt-3">
                  <div class="form-group col-lg-12 col-md-12">
                    <ListGroup>
                      <ListGroupItem action variant="secondary" className="mb-2">
                        <Row>
                          <Col>
                            <Row> <p >Date</p></Row>
                          </Col>
                          <Col>
                            <Row> <p >Heure</p></Row>
                          </Col>
                          <Col>
                            <Row><a> <i style={{cursor:"pointer", float:"left"}}  className="bx bx-trash bx-sm text-danger "/>  </a>,</Row>
                          </Col>
                        </Row>
                      </ListGroupItem>
                    </ListGroup>
                  </div>
                </div>
                

              </div>  
            </div>
          </div>


        {
          !this.state.disponible?
          <>
          <hr></hr>

          <div class="row">
            <div class="col-lg-7 mt-5 mt-lg-0 d-flex align-items-stretch" className="justify-content-center">
              <div  class="php-email-form">
                <div class="form-row">
                  <div class="container">
                    <div class="form-group col-md-12">
                      <Row className="mt-4">
                        <Col md="12">
                          <div style={{textAlign:"center"}} id="echec" class="alert alert-danger" role="alert">
                            Veuillez Selectionner le(s) jour(s) ou l'(s) heure(s) a rendre indisponible
                          </div>
                        </Col>  
                      </Row>
                      <div class="mb-3 mt-2">
                        <div class="loading">Chargement</div>
                        <div class="error-message"></div>
                        <div class="sent-message">Message</div>
                      </div>
                      <div class="text-center"><button onClick={this.handleAddDisponibilite} id="submit">Enregistrer les indisponibilités</button></div>
                    </div>
                  </div>
                </div>
              </div>  
            </div>
          </div>
           </> :null    
        }
              

        </div>
      </section>

    </CFade>):(<>{this.state.page}</>)
     }
    </Suspense>
    </CRow>
    </CContainer>

    </>
  )
}
}

export default Indisponibilite
